@font-face {
  font-family: "CormorantGaramond";
  font-display: swap;
  src: url("../fonts/CormorantGaramond-Regular.woff2"), url("../fonts/CormorantGaramond-Regular.woff");
  font-weight: 400;
}
@font-face {
  font-family: "CormorantGaramond";
  font-display: swap;
  src: url("../fonts/CormorantGaramond-Medium.woff2"), url("../fonts/CormorantGaramond-Medium.woff");
  font-weight: 500;
}
@font-face {
  font-family: "CormorantGaramond";
  font-display: swap;
  src: url("../fonts/CormorantGaramond-SemiBold.woff2"), url("../fonts/CormorantGaramond-SemiBold.woff");
  font-weight: 600;
}
@font-face {
  font-family: "CormorantGaramond";
  font-display: swap;
  src: url("../fonts/CormorantGaramond-Bold.woff2"), url("../fonts/CormorantGaramond-Bold.woff");
  font-weight: 700;
}
@font-face {
  font-family: "Poppins";
  font-display: swap;
  src: url("../fonts/Poppins-Regular.woff2"), url("../fonts/Poppins-Regular.woff");
  font-weight: 400;
}
@font-face {
  font-family: "Poppins";
  font-display: swap;
  src: url("../fonts/Poppins-Medium.woff2"), url("../fonts/Poppins-Medium.woff");
  font-weight: 500;
}
@font-face {
  font-family: "Poppins";
  font-display: swap;
  src: url("../fonts/Poppins-Bold.woff2"), url("../fonts/Poppins-Bold.woff");
  font-weight: 700;
}
html {
  overflow-x: hidden;
  box-sizing: border-box;
}

.page {
  margin: 0 auto;
  padding: 0;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  color: #1d1613;
  background-color: #ffffff;
  overflow: hidden;
}

.page--disabled {
  position: fixed;
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}

.container {
  max-width: 1170px;
  margin: 0 auto;
  padding: 0 98px;
}
@media (max-width: 1023px) {
  .container {
    padding: 0 45px;
  }
}
@media (max-width: 767px) {
  .container {
    padding: 0 15px;
  }
}

.page-main {
  flex-grow: 1;
}

.page-footer {
  flex-shrink: 0;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 68px;
  background-color: #a87b62;
  color: #ffffff;
  padding: 10px;
  font-family: "Poppins", "Arial", sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 21px;
  border: 0;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
}
.button:hover, .button:focus {
  background-color: rgba(86, 64, 52, 0.7);
}

.login {
  position: relative;
}

.login__wrapper {
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translate(-50%, 0);
  width: 430px;
  background-color: #f0eae4;
  box-shadow: 0px 5px 32px rgba(226, 221, 215, 0.24);
  background-image: url("../img/login-bg.jpg");
  background-position: 50% 0;
  background-repeat: no-repeat;
  background-size: auto 138px;
  padding: 138px 45px 55px;
  box-sizing: border-box;
  text-align: center;
}
@media (min-resolution: 2dppx) {
  .login__wrapper {
    background-image: url("../img/login-bg@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .login__wrapper {
    top: 80px;
    width: 442px;
    padding: 138px 51px 55px;
    background-image: url("../img/login-bg-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .login__wrapper {
    background-image: url("../img/login-bg-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .login__wrapper {
    top: 50px;
    width: 270px;
    padding: 84px 15px 35px;
    background-image: url("../img/login-bg-mobile.jpg");
    background-size: auto 84px;
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .login__wrapper {
    background-image: url("../img/login-bg-mobile@2x.jpg");
  }
}

.login-form {
  position: relative;
  display: flex;
  flex-direction: column;
}

.login-form h2 {
  font-family: "CormorantGaramond", "Georgia", serif;
  font-size: 34px;
  font-style: normal;
  font-weight: 700;
  line-height: 44px;
  letter-spacing: 0.03em;
  margin: 0;
  margin-top: 60px;
  margin-bottom: 45px;
}
@media (max-width: 767px) {
  .login-form h2 {
    font-size: 24px;
    line-height: 31px;
    margin-top: 40px;
    margin-bottom: 30px;
  }
}

.login-form input {
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  color: #9B9997;
  width: 100%;
  padding: 0 0 8px;
  border: none;
  border-bottom: 1px solid #1d1613;
  background-color: inherit;
}
.login-form input:hover, .login-form input:focus {
  opacity: 0.5;
  outline: none;
}
.login-form input::placeholder {
  opacity: 0.5;
}

.login-form__field:not(:last-child) {
  margin-bottom: 40px;
}
@media (max-width: 767px) {
  .login-form__field:not(:last-child) {
    margin-bottom: 25px;
  }
}

.login-form__field a {
  display: block;
  font-family: "Poppins", "Arial", sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  color: #9B9997;
  text-align: right;
  margin-top: 10px;
  text-decoration: none;
}
@media (max-width: 767px) {
  .login-form__field a {
    margin-top: 5px;
  }
}
.login-form__field a:hover, .login-form__field a:focus {
  opacity: 0.5;
}

.login-form button {
  margin-top: 10px;
  margin-bottom: 20px;
}

.login-form p {
  font-size: 14px;
  font-weight: 500;
  line-height: 21px;
  color: #9B9997;
  margin: 0;
}

.login-form p a {
  color: #1d1613;
  margin-left: 5px;
  text-decoration: none;
}
.login-form p a:hover, .login-form p a:focus {
  opacity: 0.5;
}

.login__close-button {
  width: 14px;
  height: 14px;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  background-size: 14px 14px;
  background-color: transparent;
  border: none;
  position: absolute;
  top: 20px;
  right: 20px;
  cursor: pointer;
}

.page-header {
  position: relative;
  padding: 30px 0 0;
  color: #1d1613;
}
@media (max-width: 767px) {
  .page-header {
    padding: 20px 0 0;
  }
}

.page-header::before {
  content: "";
  width: 100%;
  height: 1px;
  background-color: #F6F5F4;
  position: absolute;
  top: 115px;
  left: 0;
}

.page-header a {
  font-family: "Poppins";
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  text-decoration: none;
  color: inherit;
}

.page-header ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.page-header__top-nav {
  display: flex;
  justify-content: space-between;
  padding-bottom: 19px;
}
@media (max-width: 1023px) {
  .page-header__top-nav {
    align-items: center;
  }
}

.page-header__logo {
  margin: 0 auto;
  flex-shrink: 0;
  width: 66px;
}
.page-header__logo:hover, .page-header__logo:focus {
  opacity: 0.6;
}

.page-header__search {
  display: flex;
  align-items: center;
}
@media (max-width: 1023px) {
  .page-header__search {
    display: none;
  }
}

.page-header__login {
  margin-right: 35px;
}
@media (max-width: 1023px) {
  .page-header__login {
    display: none;
  }
}

.page-header__user-menu {
  padding-top: 17px;
}
@media (max-width: 1023px) {
  .page-header__user-menu {
    padding-top: 0;
  }
}

.page-header__main-nav {
  display: flex;
  align-items: center;
  min-height: 61px;
  justify-content: center;
}

.page-header__cart svg {
  display: none;
}
@media (max-width: 1023px) {
  .page-header__cart svg {
    display: block;
    fill: none;
  }
}

@media (max-width: 1023px) {
  .page-header__cart span {
    display: none;
  }
}

.page-header__toggle {
  display: none;
}
@media (max-width: 1023px) {
  .page-header__toggle {
    display: block;
    border: none;
    background-image: url("../img/icon-burger.svg");
    background-color: white;
    width: 25px;
    height: 18px;
    cursor: pointer;
  }
}

.search input {
  font-family: "Poppins";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 21px;
  border: none;
  padding: 5px;
}
.search input:hover, .search input:focus {
  outline: 1px solid #A87B62;
}

.search__button {
  display: flex;
  padding: 0;
  width: 14px;
  height: 14px;
  background-color: transparent;
  background-image: url(../img/icon-search.svg);
  border: none;
  cursor: pointer;
  margin-right: 10px;
}

@media (max-width: 1023px) {
  .main-nav {
    display: none;
  }
}

.main-nav__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.main-nav__link {
  display: block;
  padding: 10px;
  color: inherit;
}

.main-nav__item {
  margin-right: 35px;
}

.main-nav__about-list {
  display: none;
}

.promo {
  position: relative;
  min-height: 463px;
  background-image: url("../img/promo-bg.jpg");
  background-size: cover;
  background-position: 50% 50%;
  background-repeat: no-repeat;
  background-color: #A87B62;
  display: flex;
  align-items: center;
}
@media (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-bg@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .promo {
    background-image: url("../img/promo-bg-tablet.jpg");
  }
}
@media (max-width: 1023px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-bg-tablet@2x.jpg");
  }
}
@media (max-width: 767px) {
  .promo {
    background-image: url("../img/promo-bg-mobile.jpg");
  }
}
@media (max-width: 767px) and (min-resolution: 2dppx) {
  .promo {
    background-image: url("../img/promo-bg-mobile@2x.jpg");
  }
}

.promo::before {
  position: absolute;
  width: 100%;
  min-height: 463px;
  top: 0;
  content: "";
  display: block;
  background: rgba(86, 64, 52, 0.7);
}

.promo__info {
  position: relative;
  z-index: 3;
  color: #ffffff;
}

.promo__info p {
  font-family: "CormorantGaramond", "Georgia", serif;
  font-size: 45px;
  font-style: normal;
  font-weight: 700;
  line-height: 58px;
  letter-spacing: 0.03em;
  margin: 0;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .promo__info p {
    font-size: 38px;
    line-height: 49px;
  }
}

.promo__info span {
  font-family: "CormorantGaramond";
  font-size: 38px;
  font-style: italic;
  font-weight: 400;
  line-height: 49px;
  text-align: center;
}
@media (max-width: 767px) {
  .promo__info span {
    font-size: 34px;
    line-height: 44px;
  }
}

.new-goods {
  padding-top: 130px;
  text-align: center;
  margin-bottom: 110px;
}
@media (max-width: 1023px) {
  .new-goods {
    padding-top: 80px;
    margin-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .new-goods {
    padding-top: 70px;
    margin-bottom: 70px;
  }
}

.new-goods h2 {
  font-family: "CormorantGaramond", "Georgia", serif;
  font-size: 34px;
  font-style: normal;
  font-weight: 700;
  line-height: 44px;
  letter-spacing: 0.03em;
  margin: 0;
  color: #1d1613;
  margin-bottom: 13px;
}
@media (max-width: 767px) {
  .new-goods h2 {
    margin-bottom: 30px;
  }
}

.new-goods__buttons {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 13px;
}
@media (max-width: 767px) {
  .new-goods__buttons {
    display: none;
  }
}

.new-goods__button {
  width: 30px;
  height: 11px;
  background-color: #ffffff;
  padding: 0;
  border: none;
  cursor: pointer;
}

.new-goods__button--prev {
  background-image: url("../img/arrow-prev.svg");
}

.new-goods__button--next {
  background-image: url("../img/arrow-next.svg");
}

.new-goods a {
  text-decoration: none;
  color: #1d1613;
}

.new-goods__link {
  display: inline-block;
  border-bottom: 1px solid #A87B62;
  margin-bottom: 60px;
  font-weight: 500;
  line-height: 21px;
  text-align: center;
  color: #1d1613;
  text-decoration: none;
  outline: 0;
}
@media (max-width: 767px) {
  .new-goods__link {
    margin-bottom: 40px;
  }
}

.new-goods__list {
  margin: 0;
  padding: 0;
  list-style: none;
  column-gap: 2.5%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 30px;
}
@media (max-width: 767px) {
  .new-goods__list {
    margin-bottom: 40px;
  }
}

.new-goods__item {
  width: 270px;
  height: 100%;
  margin: 0 auto;
  margin-bottom: 20px;
}
@media (max-width: 1023px) {
  .new-goods__item {
    width: 324px;
  }
}
@media (max-width: 767px) {
  .new-goods__item {
    width: 130px;
  }
}
.new-goods__item:hover, .new-goods__item:focus {
  transform: scale(1.01);
  filter: drop-shadow(4px 4px 10px rgba(168, 122, 98, 0.24));
}

.new-goods__item h3 {
  font-size: 16px;
  font-weight: 500;
  line-height: 21px;
  text-align: center;
  margin: 0;
  margin-bottom: 15px;
}

.new-goods__item p {
  font-size: 14px;
  font-weight: 700;
  line-height: 18px;
  text-align: center;
  margin: 0;
}

.new-goods__item img {
  width: 100%;
  height: 100%;
  margin-bottom: 25px;
}

.blog {
  text-align: center;
  margin-bottom: 130px;
}
@media (max-width: 1023px) {
  .blog {
    margin-bottom: 80px;
  }
}
@media (max-width: 767px) {
  .blog {
    margin-bottom: 70px;
  }
}

.blog h2 {
  font-family: "CormorantGaramond", "Georgia", serif;
  font-size: 34px;
  font-style: normal;
  font-weight: 700;
